.container {
  padding: 80px 0 40px;
  width: 1000px;
  margin: 0 auto;
  color: #333;

  p {
    margin-bottom: 1.5em;
  }
}

#header {
  .banner {
    height: 50vh;

    .banner-bg {
      background: #888 url("/static/images/bg1.jpg") no-repeat center;
    }
  }
}

@media (max-width: 1200px) {
  .container {
    width: 900px;
  }
}

@media (max-width: 1000px) {
  .container {
    width: 700px;
  }
}

@media (max-width: 768px) {
  .container {
    width: 90vw;
    padding: 20px 0 10px;
    font-size: 14px;
  }

  #header {
    .banner {
      height: 50vh;

      .banner-bg {
        background: #888 url("/static/images/bg2.jpg") no-repeat center;
      }
    }
  }
}
